export default {
	data() {
		return {
			educationList: [{
					name: '小学',
					value: 1
				},
				{
					name: '初中',
					value: 2
				},
				{
					name: '高中',
					value: 3
				},
				{
					name: '中专',
					value: 4
				},
				{
					name: '大专',
					value: 5
				},
				{
					name: '本科',
					value: 6
				},
				{
					name: '硕士',
					value: 7
				},
				{
					name: '博士',
					value: 8
				}
			],
			jy: 1,
			multiArray: [
			],
			multiIndex: [],
			multiIndexsave:[],
			imgList: [],
			init:true
		}
	},
	onLoad(){
		this.$apiYZX.provinces().then(res=>{
			// this.provincesList = res.data.data;
			let arr=[]
			res.data.data.forEach(item=>{
				let obj={
					name:item.name,
					id:item.code
				}
				arr.push(obj)
			})	
			this.multiArray[0]=arr;
			this.getByProvinceCode(res.data.data[0].code)
		})
	},
	methods: {
		getByProvinceCode(code){
			this.$apiYZX.getByProvinceCode({provinceCode:code}).then(res=>{
				let arr=[]
				res.data.data.forEach(item=>{
					let obj={
						name:item.name,
						id:item.code
					}
					arr.push(obj)
				})	
				this.multiArray[1]=arr;
				this.getByCityCode(res.data.data[0].code)
			})
		},
		getByCityCode(code){
			this.$apiYZX.getByCityCode({cityCode:code}).then(res=>{
				let arr=[]
				res.data.data.forEach(item=>{
					let obj={
						name:item.name,
						id:item.code
					}
					arr.push(obj)
				})	
				this.multiArray[2]=arr;
				if(this.init){
					this.multiIndex=[0,0,0]
					this.init=false
				}else{
					this.multiIndex=[...this.multiIndexsave]
				}			
			})
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		MultiColumnChange(e) {
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			this.multiIndex[e.detail.column] = e.detail.value;
			this.multiIndexsave=[...this.multiIndex]
			switch (e.detail.column) {
				case 0:
					this.getByProvinceCode(data.multiArray[0][data.multiIndex[0]].id)
					break;
				case 1:
					this.getByCityCode(data.multiArray[1][data.multiIndex[1]].id)
					break;
			}			

		},
		pickerChange: function(e, val) {
			this.jy= e.target.value
		},
	}
}
